<template>
  <div class="text-center">
    <soccer-games :games="highlightedGames" :isGroup="isGroup"></soccer-games>
    <loading v-if="isLoading"></loading>
    <no-games v-if="!isLoading && highlightedGames && !highlightedGames.length"></no-games>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  const SoccerGames = () => import('./GamesComponent.vue');
  const NoGames = () => import('./../Utilities/NoGames');
  const Loading = () => import('./../Utilities/LoadingIndicator');
  export default {
    name: 'SoccerHighlights',
    props: ['isGroup', 'numberOfGames'],
    components: {SoccerGames, NoGames, Loading},
      data () {
        return {
            isLoading: true
        }
      },
    mounted () {
      this.getHighlightedGames().then(() => { this.isLoading = false }, () => this.isLoading = false)
    },
    computed: {
      ...mapGetters(['highlightedGames'])
    },
    methods: {
      ...mapActions(['getHighlightedGames'])
    }
  }
</script>

